<template>
  <div id="home">
    <nav-bar class="homenav">
      <div class="center" slot="center"></div>
    </nav-bar>
    <home-swiper :banners="banners"></home-swiper>
    <recommends-view :recommend="recommend"></recommends-view>
    <featuer-view></featuer-view>
    <tables class="tables" :titles="['','','']" @tabClick="tabClick"></tables>
    <goods-list :goods ="showGoods"></goods-list>
  </div>
</template>

<script>
import HomeSwiper from './childCom/HomeSwiper'
import RecommendsView from './childCom/RecommendsView'
import FeatuerView from './childCom/FeatuerView'

import Tables from 'content/table/Table'
import NavBar from 'common/navBar/NavBar'
import GoodsList from 'content/goods/GoodsList' 


import {getHomeData,getHomeGoods} from 'network/home'
export default {
  components:{
    HomeSwiper,
    RecommendsView,
    FeatuerView,
    NavBar,
    Tables,
    GoodsList
  },
  props:{},
  data(){
    return {
      banners:[],
      recommend:[],
      goods:{
        'pop':{page:0,list:[]},
        'new':{page:0,list:[]},
        'sell':{page:0,list:[]}
      },
      currentType:'pop'
    }
  },
  created(){
    //
    this.getHomeData()
    //goods
    this.getHomeGoods('pop') 
    this.getHomeGoods('new') 
    this.getHomeGoods('sell') 
  },
  computed:{
    showGoods(){
      return this.goods[this.currentType].list
    }
  },
  methods:{
    //
    getHomeData(){
      getHomeData().then(res=>{
        this.banners = res.data.data.banner.list;  //
        this.recommend = res.data.data.recommend.list;
      })
    },
    getHomeGoods(type){
      const page = this.goods[type].page + 1;
      getHomeGoods(type,page).then(res=>{
        // console.log(res.data.data.list)
        this.goods[type].list.push(...res.data.data.list)
        this.goods[type].page += 1
      })
    },
    //
    tabClick(index){
      switch(index){
        case 0:
          this.currentType = "pop"
          break
        case 1:
          this.currentType = "new"
          break
        case 2:
          this.currentType = "sell"
          break
      }
    }
  },
}
</script>
<style scoped>
  #home{
    padding-top: 44px;
    height: 3000px;
  }
  .homenav{
    background-color: #ff8198;
    font-weight: bold;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 9;
  }
 .center{
   color: #fff;
 }
 .tables{
   position: sticky;
   top: 44px;
 }
</style>